@use '../abstracts/variables' as v;
@use '../abstracts/mixins' as m;

.countdown {
	color: white;
	border-top: 5px solid white;
	width: 100%;
	text-align: center;
	a {
		margin-top: 2rem;
		font-size: 1.2em;
		@include m.media-breakpoint-down(lg) {
			font-size: 1em;
		}
	}
	.label {
		font-size: 3rem;
		font-weight: 700;
		text-transform: uppercase;
	}
	&__container {
		font-size: 3.5em;
		@include m.displayFlex(row, space-evenly);
		@include m.media-breakpoint-down(lg) {
			font-size: 2em;
		}
		@include m.media-breakpoint-down(md) {
			display: grid;
			grid-template-columns: repeat(auto-fit, 15rem);
		}
	}
	&__days {
		@include m.displayFlex(column, flex-start, center);
	}
	&__hours {
		@extend .countdown__days;
	}
	&__minutes {
		@extend .countdown__days;
	}
	&__seconds {
		@extend .countdown__days;
	}
}

.great-seal {
	width: 100%;
	margin: 0;
	@include m.media-breakpoint-down(md) {
		width: 75%;
		margin: 0 auto;
	}
	svg {
		aspect-ratio: 1;
		width: 100%;
	}
}

#entertainment {
	.container {
		display: flex;
		flex-direction: column;
	}
	.row:nth-child(3) {
		margin-top: 4rem;
		.col {
			align-items: center;
		}
	}
}

#vendors {
	@extend #entertainment;
	a.btn__fill--primary {
		background-color: v.$color-primary--dark;
	}
}

.big-feature {
	height: fit-content;
	&__image {
		width: 100%;
		max-height: 80rem;
		img {
			width: 100%;
			height: 100%;
			object-fit: contain;
		}
	}
	&__excerpt {
		font-size: 1.35em;
		margin-bottom: 4rem;
	}
}
